import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { getBookLoc } from '../../modules/libBook';
import { changeBar } from '../../modules/topBar';
import '../../styles/home.scss';

export default function SearchDetail() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {
        libb_title,
        libb_author,
        libb_img,
        libb_isbn,
        libb_publisher,
        libb_pub_date,
        libb_code,
        libb_state,
        libb_class
    } = useSelector(state => state.libBook.selected_book);

    useEffect(() => {
        dispatch(
            changeBar(
                "back", 
                { title: " ", data: null },
                "null",
                () => history.goBack(),
                null,
                "small"
            )
        );
    }, [dispatch, history]);
    
    //  
    const goLocation = debounce(async(class_sign) => {
        await dispatch(getBookLoc(class_sign));
        history.push("/user1/search-location");
    }, 800);

    return (
        <div id="search_detail" className="contents">

            <table id="info">
                <tbody>
                    <tr><td colSpan="2" id="book_name">{libb_title}</td></tr>
                    <tr><td colSpan="2" id="author">{libb_author}</td></tr>
                    <tr><td colSpan="2"><img src={libb_img} alt="bookImage" width="200px"/></td></tr>
                    <tr>
                        <td id="td_title">ISBN</td>
                        <td id="isbn">{libb_isbn}</td>
                    </tr>
                    <tr>
                        <td id="td_title"></td>
                        <td id="publisher">{libb_publisher}</td>
                    </tr>
                    <tr>
                        <td id="td_title"></td>
                        <td id="publish_date">{libb_pub_date.slice(0,10)}</td>
                    </tr>
                </tbody>
            </table>

            <div id="location">
                <p className="title">  </p>
                <table>
                    <tbody>
                        <tr>
                            <td id="td_title"></td>
                            <td>{libb_code}</td>
                        </tr>
                        <tr>
                            <td id="td_title"></td>
                            <td>{libb_state ? "" : ""}</td>
                        </tr>
                        <tr>
                            <td id="td_title"></td>
                            <td id="button" onClick={() => goLocation(libb_class)}></td>
                        </tr>
                    </tbody>
                </table>
            </div>

        </div>
    );
}