import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { searchStdBook } from '../../modules/userBook';
import { createChat, getChatDetail1 } from '../../modules/chat';
import { changeBar } from '../../modules/topBar';
import { AiOutlineClose, AiOutlineSearch, AiOutlineMenu } from 'react-icons/ai';

import '../../styles/student.scss';

export default function StdMain() {
    const dispatch = useDispatch();
    const history = useHistory();

    const std_num = useSelector(state => state.user.user.std_num);
    const searchResult = useSelector(state => state.userBook.search_list);  // 
    const [keyword, setKeyword] = useState("");
    const [menuState, setMenuState] = useState(false);

    useEffect(() => {
        dispatch(
            changeBar(
                "null", 
                { title: " ", data: null },
                "null",
                null,
                null,
                "small"
            )
        );
    },[dispatch]);
    
    //
    const openMenu = debounce((state) => {
        setMenuState(state);
    }, 800);

    //
    const search = debounce(() => {
        dispatch(searchStdBook(keyword));
    }, 800);

    // 
    const goChat = debounce(async(stdb_code, lender) => {
        await dispatch(createChat(stdb_code, lender, std_num, history));
        dispatch(getChatDetail1(stdb_code, std_num, history));
    }, 800);

    return (
        <div id="std_main" className="contents">
            <AiOutlineMenu id="menu_icon" size="20" onClick={() => openMenu(true)}/>
            <div id="menu" className={menuState ? "menu" : "hidden"}>
                <AiOutlineClose className={menuState ? "x_icon" : "hidden"} size="20" onClick={() => openMenu(false)}/>
                <p id="item1" className={menuState ? "menu_item" : "hidden"} onClick={() => history.push("/user1/std-create")}> </p>
                <p id="item2" className={menuState ? "menu_item" : "hidden"} onClick={() => history.push("/user1/std-my-list")}>  </p>
            </div>

            <div id="search_book" className="item">
                <input type="text" id="search_input" value={keyword||""} onChange={(e) => setKeyword(e.target.value)}/>
                <div id="search_button"><AiOutlineSearch onClick={search} size="27px"/></div>
            </div>

            <div id="search_result" className="contents">
                {searchResult.length !== 0 ?
                    searchResult.map(({ 
                        stdb_img,
                        stdb_title,
                        stdb_author,
                        stdb_publisher,
                        stdb_pub_date,
                        stdb_rental_fee,
                        stdb_rental_date,
                        stdb_comment,
                        stdb_code,
                        lender
                    }, index) => {
                        return (
                            <div className="item_wrap" key={index}>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td rowSpan="4">
                                                <img id="book_img" src={stdb_img} alt="book-img" width="120px"/>
                                            </td>
                                            <td id="td_title">{stdb_title}</td>
                                        </tr>
                                        <tr>
                                            <td id="td_content">{stdb_author} | {stdb_publisher}</td>
                                        </tr>
                                        <tr>
                                            <td id="td_content"> : {stdb_pub_date.slice(0,10)}</td>
                                        </tr>
                                        <tr>
                                            <td id="td_content">{stdb_rental_fee} ({stdb_rental_date} )</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <div className="detail">
                                    <p id="td_content">{stdb_comment}</p>
                                </div>
                                <button onClick={() => goChat(stdb_code, lender)}> </button>
                            </div>
                        )
                    })
                : <p id="message">  .</p>}
            </div>

        </div>
    );
}