import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { logout } from '../../modules/user';
import { changeBar } from '../../modules/topBar';
import { CgProfile } from 'react-icons/cg';

import '../../styles/mypage.scss';

export default function Mypage() {
    const history = useHistory();
    const dispatch = useDispatch();

    const {
        name,
        std_num
    } = useSelector(state => state.user.user);

    useEffect(() => {
        dispatch(
            changeBar(
                "null", 
                { title: "", data: null },
                "null",
                null,
                null,
                "small"
            )
        );
    }, [dispatch]);

    // ,
    const goCheckBorrow = debounce(() => {
        history.push("/user1/check-borrow");
    }, 800);

    //   
    const goCheckStdBorrow = debounce(() => {
        history.push("/user1/check-std-borrow");
    }, 800);

    //
    const goLogout = debounce(() => {
        if (window.confirm(" ?")) {
            dispatch(logout(history));
        }
    }, 800);

    return (
        <div id="mypage" className="contents">
            
            <table id="profile">
                <tbody>
                    <tr>
                        <td rowSpan="2" id="td1">
                            <CgProfile size="70"/>
                        </td>
                        <td>{name}</td>
                    </tr>
                    <tr>
                        <td id="td2">{std_num}</td>
                    </tr>
                </tbody>
            </table>

            <div id="list">
                <ul>
                    <li onClick={goCheckBorrow}>  /</li>
                    <li onClick={goCheckStdBorrow}>   </li>
                    <li onClick={goLogout}></li>
                </ul>
            </div>
            
        </div>
    );
}