import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { getMyBookList, deleteStdBook } from '../../modules/userBook';
import { changeBar } from '../../modules/topBar';

import '../../styles/student.scss';

export default function StdMyList() {
    const dispatch = useDispatch();
    const history = useHistory();
    
    const bookList = useSelector(state => state.userBook.my_book_list);
    const std_num = useSelector(state => state.user.user.std_num);

    useEffect(() => {
        dispatch(getMyBookList(std_num));
        dispatch(
            changeBar(
                "back", 
                { title: "  ", data: null },
                "null",
                () => history.goBack(),
                null,
                "small"
            )
        );
    }, [dispatch, history, std_num]);

    //  
    const deleteInfo = debounce((stdb_code) => {
        dispatch(deleteStdBook(stdb_code, history));
    }, 800);

    return (
        <div id="std_my_list" className="contents">
            {bookList.length !== 0 ? 
            bookList.map(({ 
                stdb_code, 
                stdb_img, 
                stdb_title, 
                stdb_author, 
                stdb_publisher, 
                stdb_pub_date, 
                stdb_rental_fee, 
                stdb_rental_date,
                stdb_state,
                stdb_ret_date,
                stdb_comment
            }) => {
                return(
                    <div id={stdb_code} className="list_item">
                        <table id="table1">
                            <tbody>
                                <tr>
                                    <td rowSpan="4">
                                        <img id="book_img" src={stdb_img} alt="book-img" width="120px"/>
                                    </td>
                                    <td id="td_title">{stdb_title}</td>
                                </tr>
                                <tr>
                                    <td>{stdb_author} | {stdb_publisher}</td>
                                </tr>
                                <tr>
                                    <td> : {stdb_pub_date.slice(0,10)}</td>
                                </tr>
                                <tr>
                                    <td>{stdb_rental_fee} ({stdb_rental_date})</td>
                                </tr>
                            </tbody>
                        </table>

                        <div className="detail">
                            <table id="table2">
                                <tbody>
                                    <tr>
                                        <td id="td1"> </td>
                                        <td id="td2">{stdb_state ? "" : ""}</td>
                                    </tr>
                                    <tr>
                                        <td id="td1"> </td>
                                        <td id="td2">{stdb_ret_date !== null ? stdb_ret_date : " "}</td>
                                    </tr>
                                </tbody>
                            </table>
                            <p>{stdb_comment}</p>
                        </div>

                        <button onClick={() => deleteInfo(stdb_code)}></button>
                    </div>
                )
            }) : <p id="message">  .</p>}
        </div>
    );
}