import React, { useEffect, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { uploadImg, createBook } from '../modules/admin';
import { changeBar } from '../modules/topBar';
import { roomOp, bookshelfOp, shelfOp, useInput, useInputFile } from '../common/util/Reusable';

export default function CreateBook() {
    const dispatch = useDispatch();
    const history = useHistory();

    const code = useInput("");
    const title = useInput("");
    const author = useInput("");
    const publisher = useInput("");
    const pubDate = useInput("");
    const state = useInput(true);
    const isbn = useInput("");
    const barcode = useInput("");
    const classCode = useInput("");
    const room = useInput("1");
    const bookshelf = useInput("1");
    const shelf = useInput("1");
    const libb = useInputFile([]);

    //
    const cancel = debounce(useCallback(() => {
        if (window.confirm("  ?")) {
            history.push("/admin/home");
        }
    }, [history]), 800);

    //
    const submit = debounce(useCallback(async() => {
        const formData = new FormData();
        formData.append('libb', libb.files[0]);

        const res = await dispatch(uploadImg(formData));
        dispatch(
            createBook(
                code.value, 
                title.value, 
                author.value, 
                publisher.value, 
                pubDate.value, 
                state.value, 
                isbn.value, 
                barcode.value, 
                classCode.value, 
                room.value, 
                bookshelf.value, 
                shelf.value, 
                res, 
                history
            )
        );
    }, [code, title, author, barcode, bookshelf, classCode, dispatch, history, isbn, libb, pubDate, publisher, room, shelf, state]), 800);

    useEffect(() => {
        dispatch(
            changeBar(
                "cancel", 
                { title: " ", data: null }, 
                "create", 
                cancel, 
                submit, 
                "small"
            )
        );
    }, [dispatch, cancel, submit]);


    return (
        <div id="create_book" className="contents">
            <input className="input" type="text" id="code" placeholder="" {...code}/>
            <input className="input" type="text" id="title" placeholder="" {...title}/>
            <input className="input" type="text" id="author" placeholder="" {...author}/>
            <input className="input" type="text" id="publisher" placeholder="" {...publisher}/>
            <input className="inputDate" type="date" id="pub_date" placeholder="" {...pubDate}/>
            <select className="inputSelect" placeholder="" {...state}>
                <option value={true}> </option>
                <option value={false}> </option>
            </select>
            <input className="input" type="text" id="isbn" placeholder="isbn" {...isbn}/>
            <input className="input" type="text" id="barcode" placeholder="" {...barcode}/>
            
            <input className="input" type="text" id="classCode" placeholder=" " {...classCode}/>
            <select className="inputSelect" {...room}>
                {roomOp.map(({ value, label }) => {
                    return <option key={value} value={value}>{label}</option>
                })}
            </select>
            <select className="inputSelect" {...bookshelf}>
                {bookshelfOp.map(({ value, label }) => {
                    return <option key={value} value={value}>{label}</option>
                })}
            </select>
            <select className="inputSelect" {...shelf}>
                {shelfOp.map(({ value, label }) => {
                    return <option key={value} value={value}>{label}</option>
                })}
            </select>
            <div id="inputImg">
                <label htmlFor="libb">  </label>
                <input type="file" id="libb" {...libb}></input>
            </div>
        </div>
    );
}