import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { changeBar } from '../modules/topBar';
import { resetAdmin } from '../modules/admin';
import { logout } from '../modules/user';
import { AiOutlineBook } from 'react-icons/ai';
import { CgProfile } from 'react-icons/cg';

import '../styles/admin.scss';

export default function Home() {
    const history = useHistory();
    const dispatch = useDispatch();

    const { 
        std_num,
        name
    } = useSelector(state => state.user.user);

    useEffect(() => {
        dispatch(
            changeBar(
                "null", 
                { title: "", data: null },
                "null", 
                null, 
                null, 
                "small"
            )
        );
    }, [dispatch]);

    //  
    const goCreateBook = debounce(() => {
        history.push("/admin/create-book");
    }, 800);

    // // 
    const goSearchBook = debounce(async() => {
        await dispatch(resetAdmin());
        history.push("/admin/search-book");
    }, 800);

    //
    const goLogout = debounce(() => {
        if (window.confirm(" ?")) {            
            dispatch(logout(history));
        }
    }, 800);

    return (
        <div id="home" className="contents">
            <div id="profile">
                <table>
                    <tbody>
                        <tr>
                            <td rowSpan="2" id="td1"><CgProfile size="70"/></td>
                            <td id="td2">{std_num}</td>
                        </tr>
                        <tr>
                            <td id="td2">{name}</td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <div id="menu">
                <table id="bookMenu">
                    <tbody>
                        <tr>
                            <td onClick={goCreateBook}><AiOutlineBook size="100px" id="icon1"/></td>
                            <td onClick={goSearchBook}><AiOutlineBook size="100px" id="icon2"/></td>
                        </tr>
                        <tr>
                            <td onClick={goCreateBook}> </td>
                            <td onClick={goSearchBook}>//</td>
                        </tr>
                    </tbody>
                </table>
                <p onClick={goLogout}></p>
            </div>
        </div>
    );
}