import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { register } from '../../modules/user';
import { option } from '../util/Reusable';
import { changeBar } from '../../modules/topBar';
import { useInput } from '../../common/util/Reusable';

export default function Join() {
    const dispatch = useDispatch();
    const history = useHistory();

    const id = useInput("");
    const pw = useInput("");
    const pwCheck = useInput("");
    const name = useInput("");
    const dept = useInput("");
    const [gender, setGender] = useState(0);
    const phNum = useInput("");
    const email = useInput("");

    useEffect(() => {
        dispatch(
            changeBar(
                "back", 
                { title: "", data: null }, 
                "null", 
                () => history.goBack(), 
                null, 
                "small"
            )
        );
    }, [dispatch, history]);

    //
    const submit = debounce(() => {
        if (id.value === "" || pw.value === "" || pwCheck.value === "" || name.value === "" || phNum.value === "" || email.value === "") {
            alert("  .");
            return;
        }
        //input  
        if (id.value.length !== 10) {
            alert(" 10  .");
            return;
        }
        if ((/([^a-zA-z0-9])/).test(pw.value)) {
            alert(" ,   .");
            return;
        }
        if (pw.value !== pwCheck.value) {
            alert("   .");
            return;
        }
        if ((/([^-\x20])/i).test(name.value)) {
            alert("   .");
            return;
        }
        if (!(/[0-9]{10,11}$/).test(phNum.value)) {
            alert("  .");
            return;
        }
        if (!(/[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i).test(email.value)) {
            alert("  .");
            return;
        }
        dispatch(
            register(
                id.value, 
                name.value, 
                dept.value, 
                gender, 
                phNum.value, 
                email.value, 
                pw.value, 
                history
            )
        );
    }, 800);

    return (
        <div id="join" className="start_contents">
            <input className="input" type="text" id="id" placeholder="" required {...id}/>
            <input className="input" type="password" id="pw" placeholder="" required {...pw}/>
            <input className="input" type="password" id="pwCheck" placeholder=" " required {...pwCheck}/>
            <input className="input" type="text" id="name" placeholder="" required {...name}/>
            <select className="inputSelect" placeholder="" required {...dept}>
                { option.map(({ value, label }) => {
                    return <option key={label} value={value}>{label}</option>
                })}
            </select>
            <div id="gender_input">
                <label htmlFor="0"></label>
                <input type="radio" name="gender" value={0} id="0" onChange={(e) => setGender(e.target.value)} checked required/>
                <label htmlFor="1"></label>
                <input type="radio" name="gender" value={1} id="1" onChange={(e) => setGender(e.target.value)}/>
            </div>
            <input className="input" type="text" id="phNum" placeholder="ex)01033336666" required {...phNum}/>
            <input className="input" type="email" id="email" placeholder="" required {...email}/>

            <button className="btnBlue" onClick={submit}></button>
        </div>
    );
}