import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { debounce } from 'lodash';

import { uploadImg, createStdBook } from '../../modules/userBook';
import { changeBar } from '../../modules/topBar';
import { useInput, useInputFile } from '../../common/util/Reusable';

export default function StdCreate() {
    const dispatch = useDispatch();
    const history = useHistory();

    const std_num = useSelector(state => state.user.user.std_num);

    useEffect(() => {
        dispatch(
            changeBar(
                "back", 
                { title: "  ", data: null }, 
                "create",
                cancel,
                submit,
                "small"
            )
        );
    });

    //topbar function
    const cancel = debounce(() => {
        if (window.confirm("  ?")) {
            history.push("/user/std-main");
        }
    }, 800);

    const title = useInput("");
    const author = useInput("");
    const publisher = useInput("");
    const pubDate = useInput("");
    const rentDate = useInput("7");
    const state = true;
    const rentFee = useInput(0);
    const comment = useInput("");
    const stdb = useInputFile([]);

    //
    const submit = debounce(async() => {
        const formData = new FormData();
        formData.append('stdb', stdb.files[0]);

        const res = await dispatch(uploadImg(formData))
        dispatch(
            createStdBook(
                std_num, 
                title.value, 
                author.value, 
                publisher.value, 
                pubDate.value, 
                rentDate.value, 
                rentFee.value, 
                state, 
                comment.value, 
                res, 
                history
            )
        );
    }, 800);


    return (
        <div id="createStdBook" className="contents">
            <input className="input" type="text" id="title" placeholder="" {...title}/>
            <input className="input" type="text" id="author" placeholder="" {...author}/>
            <input className="input" type="text" id="publisher" placeholder="" {...publisher}/>
            <input className="inputDate" type="date" id="pub_date" placeholder="" {...pubDate}/>
            <select className="inputSelect" {...rentDate}>
                <option value="7">7</option>
                <option value="14">14</option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="  ">  </option>
            </select>
            <input className="input" type="text" id="rent_fee" placeholder="" {...rentFee}/>
            <textarea className="textarea" placeholder="      ." {...comment}/>
            <div id="inputImg">
                <p> </p>
                <label htmlFor="stdb" id="upload"></label>
                <input type="file" id="stdb" {...stdb}></input>
            </div>
        </div>
    );
}