//initial state
const INIT_TOPBAR_STATE = {
    left: "",
    center: { title: "", data: "" },
    right: "", 
    lfunc: null, 
    rfunc: null, 
    size: ""
};


//action type
const CHANGEBAR = "CHANGEBAR";

//  
export const changeBar = (
    left, 
    center, 
    right, 
    lfunc, 
    rfunc, 
    size
) => ({
    type: CHANGEBAR,
    left,
    center,
    right,
    lfunc,
    rfunc,
    size
});


//reducer
const topbar = (state = INIT_TOPBAR_STATE, action) => {
    switch(action.type) {

        case CHANGEBAR:
            return { 
                ...state, 
                left: action.left,
                center: action.center,
                right: action.right,
                lfunc: action.lfunc,
                rfunc: action.rfunc,
                size: action.size
            }
       
        default:
            return state;
    }
}

export default topbar;